body {
    margin: 0;
    padding: 0;
    background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
}

.App {
    min-height: 100vh;
}

.search-form {
    min-height: 10vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.search-bar {
    width: 50%;
    padding: 10px;
    border: none;
}

.search-button {
    background: lightcoral;
    border: none;
    padding: 10px 20px;
    color: white;
    margin-left: 5px;
}

.receipe {
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
}

.root {
    text-align: center;
    background-image: linear-gradient(to top, #a18cd1 0%, #fbc2eb 100%);
}